:root {
    --danger: #ff8a80;
    --blue: #6292db;
    --light: #008080;
  }
  
  body {
    background-color: #008080;
    background-color: var(--light);
  }
* {
    margin: 0;
    padding: 0;
    font-family: ubuntu;
    list-style: none;
  }
.error {
    background-color: var(--danger);
    text-align: center;
    color: white;
    width: 70%;
    margin: 1rem auto;
    padding: 0.5rem 0;
    border-radius: 0.5rem;
    box-shadow: 5px;
}
.navbar {
    background-color: #436363;
    padding: 1rem 0;
    border-radius: 15px;
    
  }
  
  .navbar-brand {
    background-color: whitesmoke;
    color: var(--blue);
    text-decoration: none;
    padding: 0.5rem;
    border-radius: 0.5rem;
    text-transform: uppercase;
    font-weight: bold;
  }
  
  .navbar-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  
  .navbar-item {
    color: white;
    text-decoration: none;
    cursor: pointer;
  }
  
  .navbar-user {
    margin-top: 0.5rem;
    text-align: right;
    color: black;
  }
.container {
    margin: auto;
    width: 80%;
  }
.form {
    width: 60%;
    margin: auto;
    background-color: var(--light);
    padding: 1rem;
    border-radius: 0.1rem;
  }
  
  .form-input {
    width: 100%;
    padding: 0.5rem 1rem;
    margin: 0.5rem 0;
    border: 1px solid var(--light);
    border-radius: 0.4rem;
    box-sizing: border-box;
    outline: none;
    font-size: 1rem;
  }
  
  .form-label {
    color: white;
    text-transform: uppercase;
    font-size: 0.8rem;
  }
.button {
    background-color: white;
    border: 2px solid var(--bue);
    border-radius: 0.5rem;
    color: var(--light);
    font-size: 1rem;
    margin: 1rem;
    padding: 0.2rem 1rem;
    -webkit-transition: all 0.4s ease-out;
    transition: all 0.4s ease-out;
  }
  
  .button:hover {
    background-color: white;
  }
  
  .buttons_center {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
  }
.polls li {
    background-color: var(--light);
    color: rgb(185, 158, 158);
    padding: 0.5rem;
    text-align: center;
  }
  
  .polls li:nth-child(odd) {
    background-color: white;
  }
  
  .polls li:hover {
    border: 2px solid var(--blue);
  }
  
  .poll-title {
    color: black;
    font-size: 2rem;
    margin-top: 1rem;
    text-align: center;
    text-transform: uppercase;
  }

  .pie {
    width: 40%;
    -webkit-align-content: center;
            align-content: center;

  }

